.year-body {
  background: $yellow;
  display: flex;
  align-items: center;
}

.year-container {
  margin: 0 auto;
}

.year-header,
.year-form,
.year-footer {
  text-align: center;
}

.year-title {
  margin: 0 0 29px;
  font-size: 22px;
  text-transform: uppercase;
}

.number-input {
  height: 77px;
  width: 50px;
  padding: 0;
  margin: 0 9px;
}

.verify-button {
  display: block;
  width: 143px;
  height: 52px;
  text-transform: uppercase;
  color: white;
  margin: 73px auto 81px;
  background: $red;
  text-decoration: none;
  font-size: 24px;
  padding: 12px 0;
}

.year-footer {
  color: $red;
  font-size: 16px;
}

.year-text {
  margin: 5px;
}
